<template>
    <app-loader v-if="loading"></app-loader>
    <app-page title="Arizani korish" v-else-if="request" back="">
        <p><strong>FISH - {{request.fish}}</strong></p>
        <p><strong>Telefon - {{request.phone}}</strong></p>
        <p><strong>Summa - {{currency(request.sum)}}</strong></p>
        <p><strong>Status - <AppStatus :type="request.status"></AppStatus></strong></p>
        <div :class="['form-control']">
            <label for="status" class="">Status</label>
            <select v-model="status" id="status">
                <option value="done">Bajarildi</option>
                <option value="cancelled">Qaytarildi</option>
                <option value="active">Tasdiqlanmoqda</option>
                <option value="sending">Yuborilmoqda</option>
            </select>
        </div>
        <button class="btn danger" @click="remove">Ochirish</button>
        <button class="btn primary" @click="update" v-if="hasChanges">Ozgartirish</button>
    </app-page>
    <h3 class="text-center danger" v-else>Bunday ma'lumot bazadan topilmadi</h3>
</template>

<script>
    import AppPage from "@/components/ui/AppPage";
    import AppLoader from "@/components/ui/AppLoader";
    import AppStatus from "@/components/ui/AppStatus";
    import {useRoute} from 'vue-router';
    import {useRouter} from 'vue-router';
    import {ref,onMounted,computed} from 'vue';
    import {useStore} from 'vuex';
    import {currency} from "@/utils/currency";

    export default {
        setup(){
            const route = useRoute()
            const router = useRouter()
            const store = useStore()
            const loading = ref(false)
            const request = ref({})
            const status = ref('')


            onMounted( async ()=>{
                loading.value=true
                request.value = await store.dispatch('request/loadById',route.params.id)
                status.value = request.value.status
                loading.value=false
            })
            const hasChanges = computed( ()=>{
                return request.value.status!==status.value
            })

            const remove = async ()=>{
                await store.dispatch('request/removeById',route.params.id)
                router.push('/')
            }
            const update = async ()=>{
                const data = {...request.value,id:route.params.id,status:status.value}
                await store.dispatch('request/updateById',data)
                request.value.status = status.value

            }

            return{
                status,
                loading,
                request,
                currency,
                remove,
                update,
                hasChanges,
            }

        },
        components:{
            AppPage,
            AppLoader,
            AppStatus
        }
    }
</script>
<style scoped>
    *{
        color: #222222;
    }
</style>
